/* About Page Styles */
.about-container {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    text-align: center;
    position: relative;
    color: white;
    z-index: 2;
    padding: 20px;
    overflow: hidden;
    opacity: 0;  /* Start hidden */
    transform: translateY(50px); /* Move down initially */
    animation: fadeInUp 0.8s ease-out forwards; /* Apply animation */
  }
  
  @keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(50px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .highlight {
    color: #ffcc00;
    font-weight: bold;
  }
  
  .italic {
    font-style: italic;
  }
  
  .about-content {
    background: rgba(0, 0, 0, 0.6);
    padding: 40px;
    border-radius: 10px;
    max-width: 1200px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    margin-top: 130px;
  }
  
  .about-title {
    font-size: 36px;
    font-weight: bold;
    margin-bottom: 20px;
    text-transform: uppercase;
    letter-spacing: 2px;
    animation: fadeIn 1s ease-in-out 0.3s forwards; /* Delay animation */
    opacity: 0;
  }
  
  .about-description {
    font-size: 18px;
    line-height: 1.6;
    margin-bottom: 30px;
    animation: fadeIn 1s ease-in-out 0.6s forwards;
    opacity: 0;
  }
  
  .features-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
    margin-top: 30px;
  }
  
  .feature-card {
    background: rgba(255, 255, 255, 0.2);
    padding: 20px;
    border-radius: 8px;
    transition: transform 0.3s ease-in-out;
    animation: fadeInUp 1s ease-in-out 0.9s forwards;
    opacity: 0;
  }
  
  .feature-card:hover {
    transform: translateY(-5px);
    background: rgba(255, 255, 255, 0.3);
  }
  
  .contact-info {
    margin-top: 40px;
    animation: fadeIn 1s ease-in-out 1.2s forwards;
    opacity: 0;
  }
  
  .contact-info p {
    font-size: 16px;
    margin: 5px 0;
  }
  
  .contact-info span {
    font-weight: bold;
    color: #ffcc00;
  }
  
  /* Fade In Animation */
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  /* Responsive Fix */
  @media (max-width: 768px) {
    .about-container {
      height: auto;
      padding: 20px;
      overflow-y: auto;
      margin-top: 260px;
    }
  
    .about-content {
      padding: 20px;
    }
  
    .about-title {
      font-size: 28px;
    }
  
    .features-grid {
      grid-template-columns: 1fr;
    }
  }
  