/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Arial", sans-serif;
}

/* Fullscreen Background Video */
.page-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden; /* Prevents unwanted scrolling */
}

/* Background Video */
.background-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ensures the video covers the entire screen */
  z-index: -1; /* Puts the video behind the content */
}
.background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("../Image/restorantimg1.jpg"); /* Update with the correct path */
  background-size: cover;
  background-position: center;
  z-index: -1;
}
/* Content Styling */
.content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  font-size: 32px;
  font-weight: bold;
  padding: 20px;
  /* backdrop-filter: blur(5px);  */
}

/* Footer Styling */
.footer {
  background: rgba(0, 0, 0, 0.7);
  color: white;
  text-align: center;
  padding: 15px 0;
  width: 100%;
  position: relative;
}
html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden; /* Prevents extra scrolling */
}
