/* Global Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Room Section Container */
.room-main-section {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
    padding: 20px;
    margin-top: 250px;
}

/* Room Section Cards */
.room-section-card {
    width: 400px;
    height: 500px;
    border-radius: 15px;
    overflow: hidden;
    position: relative;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.room-section-image {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    position: relative;
}

/* Dark Overlay */
.room-overlay {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 20px;
    background: rgba(0, 0, 0, 0.5);
    color: white;
    text-align: center;
    transition: background 0.3s ease;
}

.room-overlay h2 {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
    text-transform: uppercase;
}

.room-overlay p {
    font-size: 16px;
    font-style: italic;
    line-height: 1.5;
}

/* View More Button */
.room-view-more-btn {
    width: 140px;
    height: 50px;
    border-radius: 25px;
    background: linear-gradient(135deg, #ff5722, #ff9800);
    color: white;
    font-size: 16px;
    font-weight: bold;
    border: none;
    cursor: pointer;
    margin-top: 10px;
    transition: transform 0.3s ease, background 0.3s ease;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

/* Hover Effects */
.room-section-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
}

.room-overlay:hover {
    background: rgba(0, 0, 0, 0.7);
}

.room-view-more-btn:hover {
    transform: scale(1.1);
    background: linear-gradient(135deg, #ff9800, #ff5722);
}

/* Responsive Design */
@media (max-width: 768px) {
    .room-main-section {
        flex-direction: column;
        align-items: center;
    }

    .room-section-card {
        width: 90%;
    }
}
