/* Global Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Food Section Container */
  .food-main-section {
    padding: 20px;
    margin-top: 250px;
  }
  
  /* Swiper Styles */
  .swiper {
    width: 90%;
    padding-bottom: 30px;
  }
  
  .swiper-slide {
    display: flex;
    justify-content: center;
  }
  
  /* Food Section Cards */
  .food-section-card {
    width: 400px;
    height: 500px;
    border-radius: 15px;
    overflow: hidden;
    position: relative;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  /* Background Image */
  .food-section-image {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    position: relative;
  }
  
  /* Dark Overlay */
  .food-overlay {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 20px;
    background: rgba(0, 0, 0, 0.5);
    color: white;
    text-align: center;
    transition: background 0.3s ease;
  }
  
  /* Heading */
  .food-overlay h2 {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
    text-transform: uppercase;
  }
  
  /* Description */
  .food-overlay p {
    font-size: 16px;
    font-style: italic;
    line-height: 1.5;
  }
  
  /* View More Button */
  .food-view-more-btn {
    width: 140px;
    height: 50px;
    border-radius: 25px;
    background: linear-gradient(135deg, #ff5722, #ff9800);
    color: white;
    font-size: 16px;
    font-weight: bold;
    border: none;
    cursor: pointer;
    margin-top: 10px;
    transition: transform 0.3s ease, background 0.3s ease;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  }
  
  /* Hover Effects */
  .food-section-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
  }
  
  .food-overlay:hover {
    background: rgba(0, 0, 0, 0.7);
  }
  
  .food-view-more-btn:hover {
    transform: scale(1.1);
    background: linear-gradient(135deg, #ff9800, #ff5722);
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .swiper {
      width: 100%;
    }
  
    .food-section-card {
      width: 90%;
    }
  }
  