/* General Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Arial", sans-serif;
}

body {
  background-color: #f4f4f4;
}

/* Header Styles */
.header {
  background: rgb(0 4 8 / 60%); /* Semi-transparent background */
  color: white;
  padding: 15px 0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
  transition: background 0.3s ease-in-out;
}

/* Make header fully transparent when at the top */
.header.transparent {
  background: rgba(68, 111, 157, 0);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

/* Logo Styling */
.logo img {
  height: 100px;
  width: auto;
  display: block;
  margin-left: -246px;
}

.logo {
  display: flex;
  align-items: center;
}

/* Navigation Links */
.nav-links {
  list-style: none;
  display: flex;
}

.nav-links li {
  margin: 0 15px;
}

.nav-links a {
  color: white;
  text-decoration: none;
  font-size: 18px;
  font-weight: bold;
  position: relative;
  transition: color 0.3s ease-in-out;
}

.nav-links a:hover {
  color: #ffcc00;
}

.nav-links a::after {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background: #ffcc00;
  transition: width 0.3s;
}

.nav-links a:hover::after {
  width: 100%;
}

/* Main Content */
.content {
  margin-top: 80px;
  text-align: center;
  padding: 50px 20px;
}

.content h2 {
  font-size: 36px;
  color: #333;
}

/* Footer */
.footer {
  background: #333;
  color: white;
  text-align: center;
  padding: 15px 0;
  position: relative;
  bottom: 0;
  width: 100%;
  margin-top: 50px;
}

.footer p {
  font-size: 16px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .container {
    flex-direction: column;
    text-align: center;
  }

  .nav-links {
    flex-direction: column;
    margin-top: 10px;
  }

  .nav-links li {
    margin: 10px 0;
  }

  .logo img {
    height: 40px; /* Adjust logo size for mobile */
  }
}
