* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.full-page-image {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.full-page-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.heading {
  font-size: 60px; /* Bigger and stronger */
  font-weight: 900; /* Super bold */
  text-transform: uppercase;
  text-align: center;
  color: white; /* Strong white text */
  text-shadow: 4px 4px 10px rgba(0, 0, 0, 0.6); /* Darker shadow for better visibility */
  animation: fadeSlide 1.5s ease-in-out;
  letter-spacing: 5px;
  margin-top: 60px;}
@keyframes waveEffect {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

.heading {
  animation: fadeSlide 1.5s ease-in-out, waveEffect 3s infinite ease-in-out;
}
