.service-container {
    text-align: center;
    padding: 60px 20px;
    /* background: linear-gradient(to right, #013A63, #005F99); */
    color: white;
    opacity: 0;
    transform: translateY(30px);
    transition: opacity 1s ease-out, transform 1s ease-out;
    margin-top: 130px;
  }
  
  .service-container.fade-in {
    opacity: 1;
    transform: translateY(0);
  }
  
  .service-title {
    font-size: 40px;
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 10px;
  }
  
  .service-subtitle {
    font-size: 18px;
    margin-bottom: 40px;
    color: #e0e0e0;
  }
  
  .service-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .service-card {
    background: white;
    color: #013A63;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease, box-shadow 0.3s ease, opacity 0.5s ease-in;
    opacity: 0;
    transform: translateY(20px);
  }
  
  .fade-in .service-card {
    opacity: 1;
    transform: translateY(0);
  }
  
  .service-card:hover {
    transform: translateY(-8px);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
  }
  
  .service-card img {
    width: 100%;
    height: 180px;
    object-fit: cover;
    border-radius: 10px;
  }
  
  .service-card h2 {
    font-size: 22px;
    margin-top: 15px;
  }
  
  .service-card p {
    font-size: 16px;
    margin-top: 10px;
    color: #333;
  }
  